import { gsap, SplitText } from 'vendors/gsap';

export default class AstroAnimation implements AnimationInterface {
  el?:HTMLElement;
  split?:SplitText;
  tl?:Timeline;

  constructor(els:{
    el?:HTMLElement,
  }) {
    this.el = els.el;

    this.init();

    return this;
  }

  init() {
    if (this.el) {

      this.split = new SplitText(this.el, {
        type: 'lines,words,chars',
        charsClass: 'char',
        linesClass: 'line',
        wordsClass: 'word',
        reduceWhiteSpace: false,
      });
      
      this.tl = gsap.timeline(
        { 
          paused: true,
          autoRemoveChildren: false,
          repeatRefresh: true,
        }
      )
        .fromTo(this.split.chars, 
          {
            alpha: 0,
            yPercent: 100,
          },
          { 
            alpha: 1, 
            yPercent: 0, 
            delay: 0.5,
            duration: 0.6, 
            stagger: {
              each: 0.05,
            },
            ease: 'power2.out'
          }
        )
    }
  }

  enter = () => {
    if (this.tl) {
      this.tl.timeScale(1).play(0);
    }
  }

  display = () => {
    if (this.tl) {
      this.tl.totalProgress(1, false);
    }
  }

  update = () => {
    if (this.el) {
      this.split = new SplitText(this.el, {
        type: 'lines,words,chars',
        charsClass: 'char',
        linesClass: 'line',
        wordsClass: 'word',
        reduceWhiteSpace: false,
      });
    }
  }
  
  leave = () => {
    if (this.tl) {
      this.tl.timeScale(2).reverse();
    }
  }

  dispose() {
    this.tl?.kill();
    this.split?.revert();
    this.tl = undefined;
    this.split = undefined;
  }
}
